import React from 'react'
import { Link, withRouter } from 'react-router-dom'
import { OutlinedCard } from './OutlinedCard'

function _ToyPreview(props) {
    const { toy, onDelete, loggedInUser } = props
    const onDetails = () => {props.history.push(`/toy/${toy._id}`)}
    const onEdit = () => {props.history.push(`/toy/edit/${toy._id}`)}
    const edit = loggedInUser.isAdmin? onEdit : null
    const remove = loggedInUser.isAdmin? onDelete : null

    return (
        <article className="toy-preview">
            <OutlinedCard toy={toy} onDelete={remove} onDetails={onDetails} onEdit={edit} />
            {/* <Link to={`/toy/edit/${toy._id}`}>Edit</Link> |
            <Link to={`/toy/${toy._id}`}> Details</Link> | */}
        </article>
    )
}

export const ToyPreview = withRouter(_ToyPreview)