import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { cloudinaryService } from '../services/cloudinaryService';

export function EditForm({ toy, onSaveToy }) {
  function validate(values) {
    const errors = {};
    if (!values.name) {
      errors.name = 'Name cannot be empty'
      if (values.price < 1) {
        errors.price = 'Price must be set!'
      }
      return errors;
    }
  }
  function onSubmitForm(values, { setSubmitting }) {
    setTimeout(() => {
      console.log(values)
      onSaveToy(values)
      setSubmitting(false);
    }, 400);
  }
  return (
    <div>
      <h1>Any place in your app!</h1>
      <Formik
        initialValues={toy}
        validate={validate}
        onSubmit={onSubmitForm}
      >
        {({ isSubmitting }) => (
          <Form>
            <Field type="text" name="name" placeholder="Name" />
            <ErrorMessage name="name" className="err-msg" />
            <Field type="number" placeholder="Price" name="price" />
            <ErrorMessage name="price" className="err-msg" component="span" />
            <Field as="select" name="type">
              <option value='Adult'>Adult</option>
              <option value='Educational'>Educational</option>
              <option value='Funny'>Funny</option>
            </Field>
            <label className="pointer" htmlFor="uploadImg"> Upload image</label>
            <input onChange={cloudinaryService.uploadImg} type="file" id="uploadImg" hidden/>
            <button type="submit" disabled={isSubmitting}>
                Submit
            </button>
          </Form>
        )}
      </Formik>
    </div>
  )
}
