import { ToyPreview } from './ToyPreview.jsx'
import { ToyToolBar } from './ToyToolBar.jsx'
export function ToyList({toys, onDelete, loggedInUser}) {
    return (
        <ul className="toy-list flex wrap clean-list">
            { toys.map(toy =>
                <li key={toy._id} className="toy">
                    <ToyPreview toy={toy} onDelete={onDelete} loggedInUser={loggedInUser} />
                </li>)}
        </ul>
    )
}

