import React from 'react'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { loadToys, getById } from "../store/actions/toyActions.js"
import { OutlinedCard } from '../cmps/OutlinedCard.jsx'
import Button from '@material-ui/core/Button'


class _ToyDetails extends React.Component {

    state = {
        toy: {
            name: ''
        }
    }

    componentDidMount() {
        this.props.loadToys().then(this.loadToy()) // ask how can I not load everythink when moving here....
    }

    componentDidUpdate(prevProps) {
        if (prevProps.match.params.toyId !== this.props.match.params.toyId) {
            this.loadToy()
        }
    }

    loadToy() {
        const toyId = this.props.match.params.toyId
        getById(toyId)
            .then(toy => this.setState({ toy }))
    }

    getNextPrevToy = (toyId) => {
        const toys = this.props.toys
        const toyIdx = toys.findIndex(toy => toy._id === toyId)
        return {
            prevToyId: (!toyIdx) ? toys[toys.length - 1]._id : toys[toyIdx - 1]._id,
            nextToyId: (toyIdx === toys.length - 1) ? toys[0]._id : toys[toyIdx + 1]._id
        }
    }

    render() {
        const { toy } = this.state
        const { toys } = this.props
        console.log(toy)
        if (!toy._id || !toys.length) return <h1>Loading...</h1>
        return (
            <div className="details-container">
                <OutlinedCard className="toy-details" toy={toy} description={toy.description} />
                <Link to={`/toy/${this.getNextPrevToy(toy._id).prevToyId}`}>
                    <Button variant="contained" color="primary">Prev</Button>
                </Link>
                <Link to={`/toy/${this.getNextPrevToy(toy._id).nextToyId}`}>
                    <Button variant="contained" color="primary">Next</Button>
                </Link>
            </div>
        );
    }
}

const mapGlobalStateToProps = (state) => {
    return {
        toys: state.toyModule.toys
    }
}

const mapDispatchToProps = {
    loadToys
}

export const ToyDetails = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyDetails)


