// import { utilService } from './utilService.js';
import Axios from 'axios'
var axios = Axios.create({
    withCredentials: true
})

export const toyService = {
    query,
    deleteToy,
    save,
    getById
}

// JSON-SERVER const baseUrl = 'http://localhost:3003/toy';
const baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:3030/api/toy' : '/api/toy'

async function query(filterBy) {
    console.log(filterBy)
    const queryString = Object.keys(filterBy).map(key => `${key}=${filterBy[key]}`).join('&');
    try {
        // return axios.get(baseUrl, {params:filterBy})
        const res = await axios.get(`${baseUrl}/?${queryString}`)
        return res.data
    }
    catch (err) {
        console.log('Error in toy query', err)
    }
}

async function getById(toyId) {
    try {
        const res = await axios.get(`${baseUrl}/${toyId}`)
        return res.data
    }
    catch (err) {
        console.log('Error in get toy by ID', err)
    }
}

async function deleteToy(toyId) {
    try {
        const res = await axios.delete(`${baseUrl}/${toyId}`)
        return res.data
    }
    catch (err) {
        console.log('Error in Delete toy', err)
    }
}

async function save(toyToSave) {
    if (toyToSave._id) {
        // UPDATE
        try {
            const res = await axios.put(`${baseUrl}/${toyToSave._id}`, toyToSave)
            return res.data
        }
        catch (err) {
            console.log('Error in Update a toy', err)
        }
    } else {
        // CREATE
        try {
            const res = await axios.post(baseUrl, toyToSave)
            return res.data
        }
        catch (err) {
            console.log('Error in Creating a toy', err)
        }
    }
}
