import React from 'react';
import { connect } from 'react-redux'
import { EditForm } from '../cmps/EditForm.jsx';

import { addToy, updateToy, getById } from "../store/actions/toyActions.js"
import { updateUserActivities } from "../store/actions/userActions.js"


class _ToyEdit extends React.Component {
    state = {
        toy: {
            name: '',
            price: 0,
            type: 'Adult'
        }
    }

    componentDidMount() {
        const toyId = this.props.match.params.toyId
        if (toyId) {
            getById(toyId)
                .then(toy => this.setState({ toy }))
        }
    }

    onSaveToy = (toy) => {
        if (toy._id) {
            // this.props.updateUserActivities('Edited toy', toy.name)
            this.props.updateToy(toy).then(() => this.props.history.push('/toy'))
        } else {
            // this.props.updateUserActivities('Added toy', toy.name)
            this.props.addToy(toy).then(() => this.props.history.push('/toy'))
        }
        this.setState({ toy: { name: '', price: 0 } })
    }

    render() {
        const { toyId } = this.props.match.params
        const { toy } = this.state
        if (!toy || (toyId && toy.name === '')) return <h1>Loading.....</h1>
        return (
            <div>
                <EditForm toy={toy} onSaveToy={this.onSaveToy} />
            </div>
        )
    }
}

const mapGlobalStateToProps = (state) => {
    return {
        toys: state.toyModule.toys,
    }
}

const mapDispatchToProps = {
    addToy,
    updateToy,
    updateUserActivities
}

export const ToyEdit = connect(mapGlobalStateToProps, mapDispatchToProps)(_ToyEdit)
