import React from 'react';
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { loadToys, setFilter, removeToy, setBgc } from "../store/actions/toyActions.js";
import { loadUser, updateUserActivities } from "../store/actions/userActions.js";

import { ToyList } from '../cmps/ToyList.jsx'
import { ToyFilter } from '../cmps/ToyFilter.jsx'
import Button from '@material-ui/core/Button'

class _ToyApp extends React.Component {

    componentDidMount() {
        this.props.loadToys();
    }
    onSetFilter = (filterBy) => {
        this.props.setFilter(filterBy)
        this.props.loadToys();

    }
    onDelete = (toy) => {
        // this.props.updateUserActivities('Removed a toy', toy.name)
        this.props.removeToy(toy._id)
    }

    onSetBgc = (toy, color) => {
        // this.props.updateUserActivities('Set BGC for', toy.name)
        this.props.setBgc(toy, color)
    }

    // get toysForDisplay() {
    //     let toys = this.props.toys
    //     const { type } = this.props.filterBy
    //     if (type !== 'all') toys = toys.filter(toy => toy.type === type)
    //     const regex = new RegExp(this.props.filterBy.text, 'i')
    //     toys = toys.filter(toy => regex.test(toy.name))
    //     return toys
    // }

    render() {
        const { toys } = this.props
        // const toys = this.toysForDisplay
        return (
            <section className="toy-app">
                <Link to="/toy/edit">
                    <Button variant="contained" color="primary">Add Toy</Button>
                </Link>
                { <ToyFilter onSetFilter={this.onSetFilter} />}
                { (toys || !toys.length) &&
                    <ToyList toys={toys} onSetBgc={this.onSetBgc} onDelete={this.onDelete} onToggleDone={this.onToggleDone} loggedInUser={this.props.user} />}
                {/* {this.props.modal.isOpen && <Modal msg={this.props.modal.msg} />} */}
            </section>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        toys: state.toyModule.toys,
        filterBy: state.toyModule.filterBy,
        user: state.userModule.user
    }
}

const mapDispatchToProps = {
    loadToys,
    setFilter,
    removeToy,
    setBgc,
    updateUserActivities,
    loadUser
}

export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)
