import { toyService } from "../../services/toyService.js";

export function loadToys() {
    return (dispatch, getState) => {
        const {filterBy} = getState().toyModule
        // console.log('getState()',getState().toyModule.filterBy)
        return toyService.query(filterBy)
            .then(toys => {
                const action = {
                    type: 'SET_TOYS',
                    toys
                }
                dispatch(action)
            })
    }
}

export function getById(toyId) {
    return toyService.getById(toyId)
}

export function removeToy(toyId) {
    return (dispatch) => {
        return toyService.deleteToy(toyId)
            .then(() => {
                const action = {
                    type: 'REMOVE_TOY',
                    toyId
                }
                dispatch(action)
            })
    }
}
export function setBgc(toy, color) {
    toy.bgc = color
    return (dispatch) => {
        return toyService.save(toy)
            .then((toy) => {
                const action = {
                    type: 'SAVE_TOY',
                    toy
                }
                dispatch(action)
            })
    }
}
export function addToy(toy) {
    return (dispatch) => {
        return toyService.save(toy)
            .then((savedToy) => {
                const action = {
                    type: 'ADD_TOY',
                    toy: savedToy
                }
                dispatch(action)
            })
    }
}
export function updateToy(toy) {
    return (dispatch) => {
        return toyService.save(toy)
            .then((toy) => {
                const action = {
                    type: 'SAVE_TOY',
                    toy
                }
                dispatch(action)
            })
    }
}

export function setFilter(filterBy) {
    return (dispatch) => {
        const action = {
            type: 'FILTER_TOYS',
            filterBy
        }
        dispatch(action)
    }
}
