import React from 'react'
import trash from '../assets/img/trash.png' 
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

export class OutlinedCard extends React.Component {

  render() {
    const { toy, onDelete, onDetails, onEdit, description } = this.props
    return (
      <Card style={{ minWidth: 275 }} variant="outlined">
        <CardContent>
          <Typography variant="h5" component="h2">
            {toy.name}
            {/* be{<span style={{display: 'inline-block', margin: '0 2px', transform: 'scale(0.8)'}}>â€¢</span>}nevolent */}
          </Typography>
          <Typography style={{ marginBottom: 12 }} color="textSecondary">
            {toy.type}
          </Typography>
          <Typography variant="body2" component="p">
            Price: ${toy.price}
          </Typography>
          <Typography variant="body2" component="p">
            {description}
          </Typography>
        </CardContent>
        <CardActions>
          {onEdit && <Button size="small" onClick={onEdit}>Edit</Button>}
          {onDetails && <Button size="small" onClick={onDetails}>Details</Button>}
          {onDelete && <Button size="small"><img onClick={() => { onDelete(toy) }} src={trash} /></Button>}
        </CardActions>
      </Card>
    );
  }
}


